  margin-left: -10px;
  border-radius: 50px 50px 0px 0px;
  // border: 1px solid red;
  margin-left: 0px;

 >div{
    // border: 1px solid red;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
    
   h2{
    font-size: 3.5rem;


   }
   p{
    font-size: 2rem;
